<template>
  <div class="chat-all-con">
    <section class="chat-container">
      <header class="header">{{ userInfo.username }} 的聊天室</header>
      <main class="main">
        <article class="article">
          <section class="top">
            <p class="join-time">加入时间：{{ userInfo.time }}</p>
            <ul class="zoom-wrap" id="zoom" ref="zoom">
              <li
                v-for="(item, index) in list"
                :key="index"
                class="piece"
                :style="{
                  'justify-content': item.side == 'left' ? '' : 'flex-end'
                }"
              >
                <figure
                  class="figure"
                  :style="{
                    order: item.side == 'left' ? '0' : '2'
                  }"
                >
                  <img :src="item.avatar" alt="" class="avatar" />
                </figure>
                <section class="piece_main">
                  <p
                    class="name"
                    :style="{
                      'text-align': item.side == 'left' ? '' : 'right'
                    }"
                  >
                    {{ item.owner }}
                  </p>
                  <img
                    v-if="item.type == 'image'"
                    :src="item.url"
                    class="image"
                  />

                  <p v-else class="content" :style="{ color: item.textColor }">
                    {{ item.message }}
                  </p>
                </section>
              </li>
            </ul>
          </section>
          <footer class="footer" @keyup.enter="send">
            <section class="operate-bar">
              <input
                type="color"
                class="color"
                v-model="textColor"
                @change="chooseColor"
              />
              <i class="el-icon-picture image" @click="selectFile"></i>
              <input
                type="file"
                ref="file"
                style="display:none;"
                @change="changeFile"
              />
            </section>
            <section>
              <el-input
                type="textarea"
                class="textarea"
                v-model.trim="answer"
                :style="{ color: textColor }"
              ></el-input>
              <aside class="send-wrap">
                <el-button>关闭</el-button>
                <el-button @click="send">发送</el-button>
              </aside>
            </section>
          </footer>
        </article>
        <aside class="aside">
          <p class="summary">在线人员({{ onlineList.length }})</p>
          <ul class="list">
            <li v-for="(item, index) in onlineList" :key="index" class="piece">
              <figure class="figure">
                <img :src="item.avatar" alt="" class="avatar" />
              </figure>
              <p class="username">{{ item.username }}</p>
            </li>
          </ul>
        </aside>
      </main>
    </section>
  </div>
</template>

<script>
import io from "socket.io-client";
export default {
  data() {
    return {
      url: "ws://175.24.90.8:3101",
      ws: null,
      username: "",
      list: [],
      answer: "",
      userInfo: {},
      onlineList: [],
      textColor: "#000000"
    };
  },
  created() {
    this.username = this.$route.query.username;
    this.connect();
  },
  beforeDestroy() {
    this.ws.close();
    this.ws.disconnect();
    this.ws.emit("disconnect");
  },
  methods: {
    connect() {
      this.ws = io(this.url);
      this.ws.on("connect", () => {
        this.ws.emit("join", { username: this.username, time: new Date() });
      });
      this.ws.on("joinResult", data => {
        this.userInfo = data;
      });
      this.ws.on("message", data => {
        this.list = [...this.list, data];
      });
      this.ws.on("displayUser", (res, data) => {
        this.onlineList = res;
        if (data) {
          this.$message(`欢迎${data.username}加入聊天室`);
        }
      });
    },
    send() {
      if (this.answer) {
        this.ws.emit(
          "message",
          {
            message: this.answer,
            time: new Date().toLocaleString(),
            owner: this.username,
            avatar: this.userInfo.avatar,
            type: "text",
            textColor: this.textColor
          },
          res => {
            if (res) {
              this.list = [...this.list, res];
              this.answer = "";
              this.$nextTick(() => {
                this.scrollToBottom();
              });
            }
          }
        );
      }
    },
    scrollToBottom() {
      // 滚动到最底部
      const parent = document.querySelector("#zoom");
      const length = parent.children.length;
      const ele = parent.children[length - 1];
      ele.scrollIntoView();
    },
    chooseColor(e) {
      console.log(e.target.value, this.textColor);
    },
    changeFile(e) {
      const file = e.target.files[0];
      const reader = new FileReader();
      reader.onload = () => {
        this.ws.emit(
          "message",
          {
            time: new Date().toLocaleString(),
            owner: this.username,
            avatar: this.userInfo.avatar,
            type: "image",
            url: reader.result
          },
          res => {
            if (res) {
              this.list = [...this.list, res];
            }
          }
        );
      };
      reader.readAsDataURL(file);
    },
    selectFile() {
      this.$refs.file.click();
    }
  }
};
</script>

